// === MIXIN ===
@mixin list-null {
  padding: 0;
  margin: 0;
  list-style: none;
}
// === / MIXIN ===

// === VARIABLES ===
$color-dark: #000000;
$color-dark-half: #404040;
$color-dark-half-2: #343434;
$color-dark-light: #4b4b4b;
$color-dark-light-2: #9e9e9e;
$color-dark-light-3: #828282;
$second-color: #fed74b;
$time-color: #d9e8ff;
$time-color-dark: #6ba6ff;
$base-purple: #7553ff;
$base-blue: #099ce8;

$breakpoint-sm-min: 576px;
$breakpoint-md-min: 768px;
$breakpoint-lg-min: 992px;
$breakpoint-xl-min: 1200px;
$breakpoint-xxl-min: 1400px;
// === / VARIABLES ===

// === COMMON ===
html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
}

.btn {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 165px;
  height: 44px;
  margin: 0;
  padding: 12px;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  background: none;
  border-radius: 20px;
  color: $color-dark-half;

  @media (min-width: $breakpoint-xl-min) {
    font-size: 18px;
    width: 187px;
    height: 52px;
  }

  &--offer-info {
    background-color: $second-color;
    border: 0.5px solid $second-color / 1.2;
  }
}

.more-active {
  max-height: 100% !important;
}

.wrapper-outer {
  &--main {
    flex: 1 0 auto;
  }
}

.container {
  max-width: 1105px;
  margin: 0 auto;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 26px;
  padding-bottom: 26px;
  gap: 30px;

  @media (min-width: $breakpoint-xl-min) {
    padding-top: 66px;
    padding-bottom: 66px;
    gap: 58px;
  }
}

.offer-wrap {
  display: flex;
  flex-direction: column;
  gap: 30px;

  @media (min-width: $breakpoint-md-min) {
    flex-direction: row;
  }

  @media (min-width: $breakpoint-xl-min) {
    flex-direction: column;
    width: 100%;
    gap: 58px;
  }

  &--xl-hidden {
    @media (min-width: $breakpoint-xl-min) {
      display: none;
    }
  }
}

.offer {
  max-width: 342px;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;

  @media (min-width: $breakpoint-md-min) {
    margin-left: 0;
    margin-right: 0;
  }

  @media (min-width: $breakpoint-xl-min) {
    flex-direction: row;
    max-width: 1123px;
    justify-content: flex-end;
  }

  &__img-wrap {
    position: relative;

    @media (min-width: $breakpoint-xl-min) {
      flex-grow: 1;
      max-width: 473px;
    }
  }

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;

    @media (min-width: $breakpoint-xl-min) {
      flex-direction: row;
      max-width: 1070px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 16px;
    }
  }

  &__img-remark {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    &__item-1 {
      font-weight: 600;
      font-size: 12px;
      line-height: 20px;
      display: flex;
      position: relative;
      top: 41px;
      width: 99px;
      height: 35px;
      justify-content: center;
      align-items: center;

      @media (min-width: $breakpoint-xl-min) {
        width: 154px;
        height: 42px;
        font-size: 16px;
        line-height: 20px;
        top: 46px;
      }

      &--yellow {
        background-color: $second-color;
      }

      &--blue {
        background-color: $base-blue;
        color: #ffffff;
      }
    }

    &__item-2 {
      width: 129px;
      height: 43px;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      display: flex;
      position: relative;
      justify-content: center;
      align-items: center;
      left: 0;
      top: 0;
      border-bottom-right-radius: 15px;
      border-top-left-radius: 15px;

      @media (min-width: $breakpoint-xl-min) {
        font-size: 18px;
        width: 170px;
        height: 55px;
      }

      &--purple {
        background-color: $base-purple;
        color: #ffffff;
      }
    }
  }
}

.offer-info {
  display: flex;
  flex-direction: column;
  padding-top: 14px;
  padding-left: 11px;
  padding-right: 11px;
  padding-bottom: 19px;
  border: 0.3px solid $color-dark-light-3;
  border-top: none;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  box-sizing: border-box;

  @media (min-width: $breakpoint-xl-min) {
    padding-top: 17px;
    padding-left: 22px;
    padding-right: 25px;
    border-bottom-left-radius: 0;
    border-top-right-radius: 16px;
    border-top: 0.3px solid $color-dark-light-3;
    border-left: none;
    flex-grow: 1;
    min-width: 597px;
    max-width: 615px;
  }

  &__duration {
    display: flex;
    align-items: center;
    order: 1;

    @media (min-width: $breakpoint-xl-min) {
      order: 2;
      margin-bottom: 9px;
    }

    &-value {
      font-size: 12px;
      line-height: 20px;
      color: $color-dark-light-2;
      margin-left: 9px;

      @media (min-width: $breakpoint-xl-min) {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }

  &__heading {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    order: 2;

    @media (min-width: $breakpoint-xl-min) {
      order: 1;
      font-size: 26px;
      line-height: 34px;
      margin-top: 0;
      margin-bottom: 13px;
      padding-right: 27px;
    }

    &-link {
      text-decoration: none;
      color: #000000;
    }
  }

  &__condition {
    margin-bottom: 22px;
    order: 3;

    @media (min-width: $breakpoint-xl-min) {
      margin-bottom: 12px;
    }

    &-list {
      @include list-null();
      padding-top: 4px;
    }
    &-item {
      padding-left: 36px;
      position: relative;
      margin-bottom: 13px;

      @media (min-width: $breakpoint-xl-min) {
        padding-left: 31px;
        margin-bottom: 16px;
      }

      &--time-box {
        @media (min-width: $breakpoint-xl-min) {
          display: flex;
          width: 586px;
        }
      }

      &::before {
        position: absolute;
        content: " ";
        width: 16px;
        height: 16px;
        top: 4px;
        left: 1px;
        background-image: url("../img/check-mark.svg");
        background-repeat: no-repeat;
      }

      &-text {
        font-size: 14px;
        line-height: 21px;
        color: $color-dark-light;

        @media (min-width: $breakpoint-xl-min) {
          font-size: 18px;
          line-height: 20px;
          margin-right: 8px;
        }
      }
    }
  }

  &__time {
    display: flex;
    flex-wrap: wrap;
    max-height: 25px;
    overflow: hidden;
    margin-top: 12px;

    @media (min-width: $breakpoint-xl-min) {
      margin-top: 0;
      max-height: 28px;
      width: 345px;
    }

    &-title {
      margin-bottom: 7px;

      &:not(:nth-child(4n + 4)) {
        margin-right: 7px;
      }
    }

    &-title {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 63px;
      height: 24px;
      background-color: $time-color;
      font-size: 14px;
      line-height: 20px;
      color: $color-dark-half-2;
      border-radius: 11px;
      text-decoration: none;
      cursor: pointer;

      @media (min-width: $breakpoint-xl-min) {
        font-size: 16px;
        line-height: 20px;
        width: 73px;
        height: 28px;
      }

      &:hover {
        background-color: $time-color-dark;
      }
    }
  }

  &__checkout-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 7px;
    padding-right: 8px;
    order: 4;
    flex-wrap: wrap;

    @media (min-width: $breakpoint-xl-min) {
      justify-content: flex-start;
      padding-left: 0;
    }
  }
}

.tile-price {
  @media (min-width: $breakpoint-xl-min) {
    margin-right: 11px;
  }

  &-small {
    font-size: 30px;
    line-height: 20px;
    color: $color-dark;

    @media (min-width: $breakpoint-xl-min) {
      font-size: 36px;
      margin-bottom: 7px;
    }
  }

  &-full {
    font-size: 10px;
    line-height: 20px;

    &__mb {
      @media (min-width: $breakpoint-xl-min) {
        display: none;
      }
    }

    &__ds {
      display: none;

      @media (min-width: $breakpoint-xl-min) {
        display: inline-block;
        vertical-align: middle;
      }
    }

    @media (min-width: $breakpoint-xl-min) {
      font-size: 14px;
    }
  }
}

// === / COMMON ===
